
/* ----------- create projects form ----------- */
#create-project-holder {
    margin-top: 15px;
    width: 400px;
}

#create-project-header {
    background: #1F2833;
    color: #66FCF1;
    margin-bottom: 8px;
    padding: 12px 20px;
    border: 2px solid #66FCF1;
    border-radius: 15px;
    text-align: center;
  }
  
  #create-project-form {
    background: #1F2833;
    display: flex;
    align-self: flex-start;
    display: grid;
    justify-items: center;
    align-items: center;
  }
  
  .create-project-form-field {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    outline: none;
    background-color: #1F2833;
    border: 2px solid #66FCF1;
    color: #66FCF1;
    border-radius: 15px;
    box-sizing: border-box;
  }

  .create-project-form-field input {
    color: #66FCF1;
  }
  
  .create-project-form-submit {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    border-width: 1px;
    border-style: solid;
    border-radius: 15px;
    border: 2px solid #3a3a3a;
    color: white;
    font-weight: bold;
    background-color: #3a3a3a;
    cursor: pointer;
    outline: none;
  }
  
  .create-project-form-submit:hover{
    background: #1F2833;
    border-style: solid;
    border: 2px solid #66FCF1;
    color: #66FCF1;
  }
  
  /* ----------- projects page + objects ----------- */
  .projects-main-div {
    min-height: 100vh;
    background: #1F2833;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  
  .project-object {
    height: 400px;
    width: 400px;
    margin: 15px 0px 15px 0px;
    padding: 10px 15px;
    background: #3a3a3a;
    border: 2px solid #66FCF1;
    color: white;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: left;
    transition: 0.3s;
  }

  .project-object:hover{
    border-radius: 15% 15% 15% 15%;
    background: #1F2833;
    color: #66FCF1;
    cursor: pointer;
  }

  .project-title {
      text-align: center;
  }

  .project-description {
    text-align: center;
  }

  .join-url {
    text-decoration: underline;
  }

  .join-url:hover {
    cursor: pointer;
  }