nav {
  position: fixed; /* Set the navbar to fixed position */
  width: 100%; /* Full width */
}

.nav-links {
  list-style-type: none;
  height: 8vh;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color:  #3a3a3a;
}

.nav-item {
  height: 8vh;
  float:right;
  text-decoration: none;
  display: block;
  text-align: center;
  padding: 16px 30px;
  background-color:  #3a3a3a;
  color: white;
}

.nav-item:hover {
  background-color: grey;
  color: #66FCF1;
}

.logo {
  float: left;
  padding: 0;
  padding: 3px 50px;
}

.nav-user {
  float: left;
}

img {
  width: 50px;
}
